import React, { useEffect, useRef } from 'react';
import { FiX } from 'react-icons/fi';

function Info({ closeInfo }) {
    let editRef = useRef();

    // CLICK OUTSIDE -> CLOSE INFO
    useEffect(() => {
        let handler = (event) => {
            if (!editRef.current.contains(event.target)) {
                closeInfo('');
            }
        };

        document.addEventListener('mousedown', handler);

        return () => {
            document.removeEventListener('mousedown', handler);
        };
    });

    return (
        <div className='info-container' ref={editRef}>
            <button className='btn-icon' onClick={() => closeInfo('')}>
                <FiX className='react-icon' />
            </button>
            <p>
                Tracka dina rutiner, skriv att-gra listor och anteckna allt
                mellan himmel och jord.
                <br />
                <br />
                Redigera en rutin genom att trycka p namnet eller ka antalet
                genom att trycka p rutan fr rutinen.
                <br />
                <br />
                Att-gra-punkter kan du vlja att checka av eller radera helt.
            </p>
        </div>
    );
}

export default Info;
